/* 0. Reset ================================= */
@import 'reset';
$primary-color: #94d6ba;
$secondary-color: #7f4882;
$accent-color: #00999e;
$accent-color2: #162343;

/* 1. Main Layout ================================= */

* {
	box-sizing: border-box;
}
html{
	background-color: $accent-color;
	font-family: 'Arvo', serif;
}

header{
	background: $accent-color2;
	color: $primary-color; 
	width: 100%;

	.page{
		display: flex;
		justify-content: space-between;
		padding: 20px 5% 20px;
	}
		
	h1{
		text-transform: uppercase;
		padding: 15px 0;
		font-weight: bold;
		font-size: 2.4em;
	}

	span{
		color: $accent-color;
	}
	
	nav{
		.hamburger{
			button{
				background: none;
				border: none;
				width: 65px;
				padding: 10px;
				transition: .25s;
				
				div{
					background: $accent-color;
					height: 8px;
					border-radius: 0px;
					margin: 0 0 8px;
				}

				&:hover{
					transform: rotate(90deg);
				}
			}	
		}

		.first-nav{
			position: absolute;
			top: 0;
			left: 0;
			background-color: transparentize(#000, .2); 
			height: 975px;
			width: 100%;
			z-index: 20;
			padding-top: 40px;

			li{
				margin: 55px 6% 0; //115/1440
				font-size: 20px;	
				text-align: center;
			}

			a{
				color: $primary-color;
				text-transform: uppercase;
				text-decoration: none;
				font-size: 25px;
				padding: 15px;
				transition: .3s;				

				&:hover{
					color: $secondary-color;
					cursor: pointer;
				}
			}

			button{
				position: absolute;
				top: 0;
				right: 0;
				color: $primary-color;
				font-size: 40px;
				float: right;
				padding: 20px;
				cursor: pointer;
				font-family: arial;
				background: transparent;
				border: none;
				transition: .3s;

				&:hover{
					color: $secondary-color;
				}
			}				
		}

		.second-nav{
			display: none;
		}
	}		
}

.home-section{
	background-image: url(../img/homebg.jpg);
	background-position: center;
	background-repeat: no-repeat; 
	height: 485px;
	
	.hero{
		padding: 30px 5%;
		float: left;
		background-color: $primary-color;
		opacity: .9;
		margin: 60px 0; 
		display: flex;
		flex: 1 100%;
		
		h2{
			font-size: 1.65em;
			color: $accent-color2;
			flex: 2;
			transition: .5s;
		}	

		p{
			font-size: 60px;
			color: $accent-color;
			flex: 1;
			transition: .5s;
		}	
	}
}

/*Large Phone*/
@media screen and (min-width: 550px){
	.home-section{
		.hero{
			h2{
			 	font-size: 2em;
			 	transition: .5s;
			}
			p{
				font-size: 80px;
				transition: .5s;
			}	
		}
	}
}

/*Small Tablet*/
@media screen and (min-width: 768px){
	.home-section{
		.hero{
			h2{
			 	font-size: 2.5em;
			 	transition: .5s;
			}
		}
	}
}

/*Large Tablet*/
@media screen and (min-width: 1024px){
	header{
		nav{
			.hamburger{
				display: none;
			}
			div{
				display: none;
			}
			.second-nav{
				display: flex; 

				li{
					margin-top: 22px;
				}

				a{	
					text-decoration: none;
					font-size: 1.3em;
					color: $secondary-color;
					text-transform: uppercase;
					padding: 0 12%;
					border
					
					&:hover{
						color: $primary-color;
					}
				}
			}
		}
	}
}

/*Desktop*/
@media screen and (min-width: 1240px){
	.wrapper{
		background-color: #c7c9c6;
		float: left;
		margin: 0 16.19718309859155%;
	}

	header{
		height: 70px;
		
		h1{
			font-size: 2.6em;
		}

		nav{
			.second-nav{
				
			}	

			a{
				background: $accent-color2;
				font-size: 15px;
			}
		}
	}
}


